<template>
<div>
  <mu-appbar>
    <mu-icon-button icon='arrow_back' @click="back" slot="left"/>
    <div class="play-title">
      <div class="play-name"><span>{{title}}</span></div>
    </div>
  </mu-appbar>
  <mu-tabs :value="activeTab" @change="handleTabChange" class="view-tabs">
    <mu-tab value="1" title=""/>
    <mu-tab value="2" title=""/>
    <mu-tab value="3" title=""/>
  </mu-tabs>
  <mu-popup position="top" :overlay="false" :open="topPopup" >
    <div class="demo-popup-top"></div>
  </mu-popup>
  <mu-circular-progress :size="50" :strokeWidth="5" style="position: fixed;z-index: 999;left: 50%;margin-left: -25px;top:30%"  v-if="isloading"/>

  <div class="container p10">
      <div v-if="activeTab === '1'">
        <div style="color: #f60;">
          <div style="color: #36f;">ICP<br/>http://icp.sundns.com/login.php</div>
          
        </div>

        <p>
          <mu-text-field label="" hintText="" v-model="webinfo.bizicbUsername"/>
          <mu-text-field label="" hintText="" v-model="enterprise.email"/>
          <mu-text-field label="" hintText="" v-model="webinfo.bizicbPassword"/>
        </p>
        <p>
          <mu-raised-button label="" @click="submit('2')" class="demo-raised-button" primary fullWidth/>
        </p>
      </div>

      <div v-if="activeTab === '2'">
        <p>
          <mu-text-field label="" hintText="" v-model="bind.address"/>
          <mu-select-field v-model="enterprise.type" :labelFocusClass="['label-foucs']" label="">
            <mu-menu-item v-for="v,index in enterpriseTypeSelect" :value="v.value" :title="v.text" />
          </mu-select-field>
          <mu-select-field v-model="enterprise.certType" :labelFocusClass="['label-foucs']" label="">
            <mu-menu-item v-for="v,index in enterpriseCertTypeSelect" :value="v.value" :title="v.text" />
          </mu-select-field>
          <mu-text-field label="" hintText="" v-model="enterprise.certNumber"/>
        </p>
        <p>
          <mu-raised-button label="" @click="submit('3')" class="demo-raised-button" primary fullWidth/>
        </p>
      </div>

      <div v-if="activeTab === '3'">
        <p>
          <mu-text-field label="" hintText="" v-model="enterprise.name"/>
          <mu-text-field label="" hintText="" v-model="enterprise.dist"/>
          <mu-text-field label="" hintText="" v-model="enterprise.certNumber"/>
          <mu-text-field label="" hintText="" v-model="enterprise.legalPre"/>
          <mu-text-field label="" hintText="" v-model="enterprise.legalPersonIdNumber"/>
          <mu-text-field label="" hintText="" v-model="enterprise.legalPersonCellphone"/>
          <mu-text-field label="" hintText="" v-model="bind.address"/>
          <mu-text-field label="" hintText="" v-model="enterprise.email"/>
          <mu-text-field label="" hintText="" v-model="enterprise.phone"/>
          <mu-text-field label="IP" hintText="IP" v-model="bind.ip"/>

          <input type="file" accept="image/*" @change="uploadImage($event)" style="display:none" id="uploadImage">
          <mu-text-field label="" hintText="" v-model="enterprise.certPic" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(1)"/>

          <mu-text-field label="" hintText="" v-model="enterprise.legalPersonPhoto" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(2)"/>

          <mu-text-field label="" hintText="" v-model="principal.certReversePic" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(3)"/>

          <mu-text-field label="" hintText="" v-model="enterprise.legalPersonBust" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(4)"/>

          <div style="color:#999;padding-bottom:10px;clear: both;">
            []: , <a href="javascript:;" @click="open('bottom')"></a><a href="http://icp.sundns.com/web/a/beianliucheng/2013/0703/134.html" target="_blank" style="color:#999;padding-left: 30px;"></a><br/>
             <a href="http://icp.sundns.com/web/a/ziliaoxiazai/2013/0701/133.html" target="_blank"></a><a href="http://icp.sundns.com/web/a/beianliucheng/2013/0711/137.html" target="_blank"></a>
          </div>

          <mu-text-field label="" hintText="" v-model="webinfo.checkPic" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(5)"/>

          <div style="color:#999;padding-bottom:10px;clear: both;">
             <a href="http://icp.sundns.com/web/a/ziliaoxiazai/2010/0706/88.html" target="_blank"></a>,<a href="http://cdn.jihui88.com/fujian/jihui/.doc" target="_blank" style="padding-left:5px;color: #999;"></a>
          </div>

          <mu-text-field label="" hintText="" v-model="webinfo.dutyPic" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(6)"/>

          <div style="color:#999;padding-bottom:10px;clear: both;">
            <a href="http://cdn.jihui88.com/fujian/jihui/.doc" target="_blank" style="padding-left:5px;color: #999;"></a>
          </div>

          <mu-text-field label="" hintText="" v-model="enterprise.domainCertPic" class="img-upload"/>
          <mu-raised-button label="" class="demo-raised-button upload-fl" @click="uploadImg(7)"/>

          <div style="color:#999;padding-bottom:10px;clear: both;">
            []: 
          </div>



          <hr style="clear:both;"/>
          <mu-text-field label="" hintText="" v-model="principal.name" class="principal-inp-left"/>
          <mu-checkbox label="" class="demo-checkbox" v-model="same1"/>
          <mu-text-field label="" hintText="" v-model="principal.certNumber" class="principal-inp-left"/>
          <mu-checkbox label="" class="demo-checkbox" v-model="same2"/>
          <mu-text-field label="" hintText="" v-model="principal.cellphone" class="principal-inp-left"/>
          <mu-checkbox label="" class="demo-checkbox" v-model="same3"/>
        </p>
        <p style="color:#999;font-size:12px">
          <br/>
                32312   4007200020
        </p>
        <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="bottomPopup" @close="close('bottom')">
          <mu-appbar title="">
            <mu-flat-button slot="right" label="" color="white" @click="close('bottom')"/>
          </mu-appbar>
          <mu-content-block>
            <p>
              :<br/>
              21<br/>
              13534934324<br/>
              :<br/>
              C1-205<br/>
              0571-324234234
            </p>
            <p>
              : () + + + 2 + 2
            </p>
          </mu-content-block>
        </mu-popup>
        <p>
          <mu-raised-button label="" @click="submit('3')" class="demo-raised-button" primary fullWidth/>
        </p>
      </div>


  </div>
</div>
</template>

<style lang="less" scoped>
  @import "../../assets/theme.less";
  .view-tabs {
    background-color: #fff;
    color: rgba(0,0,0,.87);
    >.mu-tab-link {
      color: rgba(102,102,102,1);
    }
    >.mu-tab-active{
      color: @primaryColor;
    }
  }
  .demo-popup-top{
    width: 100%;
    opacity: .8;
    height: 48px;
    line-height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 375px;
    padding: 0 30px;
  }
  .principal-inp-left{
    width: 68%;margin-right:3%
  }
  .img-upload{
    width: 65%;margin-right:3%;    float: left;
  }
  .upload-fl{
    float: left;margin-top: 15px;
  }
</style>
<script>
import lrz from 'lrz'
export default {
  data () {
    return {
      activeTab: '1',
      isloading: false,
      title: '',
      topPopup: false,
      bottomPopup: false,
      dialog: false,
      same1: false,
      same2: false,
      same3: false,
      upload: 0,
      enterpriseTypeSelect: [
        {text: '', value: '04'},
        {text: '', value: '05'},
        {text: '', value: '03'},
        {text: '', value: '01'},
        {text: '', value: '00'},
        {text: '', value: '02'},
        {text: '', value: '06'},
        {text: '', value: '07'},
        {text: '', value: '08'},
        {text: '', value: '09'}
      ],
      enterpriseCertTypeSelect: [
        {text: '', value: '00'},
        {text: '', value: '01'},
        {text: '', value: '02'},
        {text: '', value: '03'}
      ],
      user: {},
      enterprise: {},
      principal: {},
      bind: {},
      webinfo: {},
      emergency: {}
    }
  },
  created () {
    this.get()
  },
  watch: {
    same1: function () {
      if (this.same1) {
        this.principal.name = this.enterprise.legalPre
      }
    },
    same2: function () {
      if (this.same2) {
        this.principal.certNumber = this.enterprise.legalPersonIdNumber
      }
    },
    same3: function () {
      if (this.same3) {
        this.principal.cellphone = this.enterprise.legalPersonCellphone
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    handleTabChange (val) {
      this.activeTab = val
    },
    uploadImage (e) {
      let _this = this
      lrz(e.target.files[0], {width: 800, fieldName: 'Filedata'})
        .then(function (rst) {
          /* ==================================================== */
          // ajax (_)
          // jQueryformDatagooglebaidu
          let xhr = new XMLHttpRequest()
          xhr.open('POST', '/rest/api/album/fileupload')
          xhr.onload = function () {
            _this.isloading = false
            if (xhr.status === 200) {
              // 
              if (_this.upload === 1) { _this.enterprise.certPic = JSON.parse(xhr.response).attributes.data }
              if (_this.upload === 2) { _this.enterprise.legalPersonPhoto = JSON.parse(xhr.response).attributes.data }
              if (_this.upload === 3) { _this.principal.certReversePic = JSON.parse(xhr.response).attributes.data }
              if (_this.upload === 4) { _this.enterprise.legalPersonBust = JSON.parse(xhr.response).attributes.data }
              if (_this.upload === 5) { _this.webinfo.checkPic = JSON.parse(xhr.response).attributes.data }
              if (_this.upload === 6) { _this.webinfo.dutyPic = JSON.parse(xhr.response).attributes.data }
              if (_this.upload === 7) { _this.enterprise.domainCertPic = JSON.parse(xhr.response).attributes.data }
            } else {
              // 
            }
          }
          xhr.onerror = function () {
            // 
            _this.isloading = false
          }
          xhr.upload.onprogress = function (e) {
            // 
            _this.isloading = true
            // var percentComplete = ((e.loaded / e.total) || 0) * 100
          }
          // 
          rst.formData.append('fileLen', rst.fileLen)
          // 
          xhr.send(rst.formData)
          /* ==================================================== */
          return rst
        })
        .catch(function (err) {
          console.log(err)
          // 
        })
        .always(function () {
          // 
        })
    },
    open (position) {
      this[position + 'Popup'] = true
    },
    close (position) {
      this[position + 'Popup'] = false
    },
    uploadImg: function (val) {
      this.upload = val
      document.querySelector('#uploadImage').click()
    },
    get () {
      this.$http.get('/rest/api/profile/detail').then((res) => {
        this.user = res.data.attributes.data.user
        this.enterprise = res.data.attributes.data.enterprise
        this.principal = res.data.attributes.data.principal
        this.bind = res.data.attributes.data.bind
        this.webinfo = res.data.attributes.data.webinfo
        this.emergency = res.data.attributes.data.emergency
        if (this.principal.name != null && this.principal.name === this.enterprise.legalPre) {
          this.same1 = true
        }
        if (this.principal.certNumber != null && this.principal.certNumber === this.enterprise.legalPersonIdNumber) {
          this.same2 = true
        }
        if (this.principal.cellphone != null && this.principal.cellphone === this.enterprise.legalPersonCellphone) {
          this.same3 = true
        }
      })
    },
    submit (val) {
      this.$http.post('/rest/api/profile/detail/all', {
        user: this.user,
        enterprise: this.enterprise,
        principal: this.principal,
        bind: this.bind,
        webinfo: this.webinfo,
        emergency: this.emergency
      },
      {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}).then((res) => {
        this.activeTab = val
        document.body.scrollTop = 0
        if (val) {
          this.topPopup = true
          setTimeout(() => {
            this.topPopup = false
          }, 2000)
        }
        this.principal.principalId = res.data.attributes.data.principal.principalId
        this.bind.bindId = res.data.attributes.data.bind.bindId
        this.webinfo.webinfoId = res.data.attributes.data.webinfo.webinfoId
        this.emergency.principalId = res.data.attributes.data.emergency.principalId
      })
    }
  }
}
</script>



